<template>
  <div class="content">
    <div class="banaer">
    <div class="container">
    <div class='head'>我的漫画</div>
    <el-table :data="tableData"style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left">
            <el-form-item label="ID">
              <span>{{ props.row.cid }}</span>
            </el-form-item>
            <el-form-item label="详情">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
            <el-form-item label="封面">
              <img :src="props.row.cover" width="240" height="320"/>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="漫画">
          <template slot-scope="scope">
            <a @click="goLink(scope.$index, scope.row , '/pubcomics')">{{ scope.row.name }}</a>
          </template>
      </el-table-column>
      <el-table-column label="作者" prop="author" width="140">
      </el-table-column>
      <el-table-column label="分类" width="50" prop="tag">
      </el-table-column>
      <el-table-column label="状态" align="center">
        <template slot-scope="scope" >
          <el-tag color='#F0F8FF' type="info" v-if="scope.row.flag">发布</el-tag>
          <el-tag color='#FFF5EE' type="info" v-if="!scope.row.flag">存稿</el-tag>
          <el-tag color='#F0F8FF' type="info" v-if="scope.row.flag&&scope.row.state">通过</el-tag>
          <el-tag color='#FFF5EE' type="info" v-if="scope.row.flag&&!scope.row.state">待审</el-tag>
        </template>
      </el-table-column>
      
      <el-table-column label="发布时间" width="156" getLocalTime :formatter="dateFormat" prop="date">
      </el-table-column>
      <el-table-column label="更新章节"  width="80" prop="chapter" align="center">
      </el-table-column>
      <el-table-column label="评分"  width="50" prop="rate">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-popover ref="popover{{$index}}" class="aew" placement="top" >
            <p>删除 {{ scope.row.title }} ？</p>
            <div style="text-align: right; margin: 0">
              <el-button size="mini" type="text" @click="cancel()">取消</el-button>
              <el-button size="mini" type="danger" round @click="handleDelete(scope.$index, scope.row)">确定</el-button>
            </div>
          </el-popover>
          <el-button size="mini" type="danger" v-popover:popover{{$index}} ><i class="el-icon-delete"/></el-button>
          <el-button size="mini" type="danger" @click="goLink(scope.$index, scope.row , '/chapter')">章节</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pager">
      <el-pagination v-show="totalCount!==0" layout="prev, pager, next" :current-page="currentPage" :total=totalCount @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
  </div>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
import {mapMutations} from 'vuex'
export default {
  data() {
    return {
      searchParam: {},
      currentPage: 1,
      totalCount: 0,
      tableData: []
    }
  },
  created() {
    this.searchParam.param = JSON.parse(sessionStorage.getItem('u')).uid
    this.getData()
  },
  methods: {
    handleCurrentChange(val) {
      this.searchParam.page = val-1
      this.currentPage = val
      this.getData()
    },
    handleDelete(index, row) {
      document.querySelector("#app").click()
      var _this = this
      this.$ajax.post('/api/deleteComics',row).then(function(response){
        if(response.data.code === 0) 
        _this.$message({message: response.data.data,type: 'success'});
        _this.getData()
      }).catch(function(error){console.log(error)})
    },
    dateFormat(row, column) {  
      return this.getDateTime(row[column.property])
    },
    getData() {
      var _this = this
      this.$ajax.post('/api/getComicsByUid',_this.searchParam).then(function(response){
        _this.tableData = response.data.data.content
        _this.totalCount = response.data.data.totalElements
        console.log(response.data)
      }).catch(function(error){console.log(error)})
    },
    cancel() {
      document.querySelector("#app").click()
    },
    goLink(index, row, path) {
      this.$router.push({ 
        path: path,
        query:{cid:row.cid,n:row.name}
      })
    }
  }
}
</script>

<style lang='stylus' rle='stylesheet/stylus'>
.banaer
  padding-top 10px
  // background url(../images/new/m_banner_bak1.png)
  background-position top center
  background-repeat no-repeat
  min-width 1000px
  .container
    width 1000px
    margin 0 auto
    overflow hidden
    position relative
    .userCenterBar
      background-color #fff
      margin 20px 0
a
  text-decoration none
a:hover
  cursor pointer
  color #ff3860
.head
  font-size 18px
  margin-bottom 14px
.el-tag--info
  border-color #fff
.el-pagination .btn-next, .el-pagination .btn-prev, .el-pagination
  color #909399
.el-pager li.active
  color	#FFB6C1
.pager
  margin 35px
  text-align center
</style>



